.button a {
    font-size: 14px;
    color: #fff;
    padding: 12px 30px;
    border-radius:#00092A ;
    letter-spacing: 0.5px;
    transition: all .3s;
    border-radius: 25px;
   
  }
  .primary-btn a{
    background-color: #403255;

  }
  .primary-btn:hover a {
    background-color: #00092A;
    color: #635d5f;
    text-decoration: none;
  }
  
  .secondary-btn a{
    border: 1px solid #00092A;

  }
  .secondary-btn a:hover {
    border: 1px solid #00092A;
    color: #635d5f;
    text-decoration: none;

  }
  